.pedidos-recebidos-titulo {
    height: 12vw;
    color: black;
    font-size: 10vw;
    background-color: rgb(139, 201, 236);
}

.pedidos-recebidos-titulo h3 {
    font-size: 5vw;
    margin-top: 0vw;
    padding-top: 2vw;
    padding-left: 2vw;
}

#pedidos-recebidos {
    background-color: rgb(231, 229, 229);
    min-height: 100vh;
}

.error-div {
    font-size: larger;
}

.loading-div {
    margin-top: 2vh;
}

.refresh {
    height: 7vw;
    width: 9vw;
}

#error {
    display: flex;
    flex-direction: row;

    border-radius: 3vw;
    border-width: 0.2vw;
}

#error {
    background-color: red;
    border-style: solid;

    margin-top: 2vh;
    margin-inline: 2vw;

    padding-top: 1vh;
    padding-bottom: 3vh;
    padding-left: 2vw;
    font-size: large;
    font-weight: bolder;
}

.refresh {
    font-size: 5vw; 
    border-style:none;
    border-color: black;
    margin-left: 35vw;
    color: black;
    background-color: rgb(139, 201, 236);
}

